
const SHEETDB_API_URL = 'https://sheetdb.io/api/v1/ux2pqa8mw3tmr'; 

const addExpenseToSheet = async (expense) => {
  try {
    console.log("Trying");
    const response = await fetch(SHEETDB_API_URL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        data: [
          {
            description: expense.description,
            amount: expense.amount,
            image: expense.image, 
          },
        ],
      }),
    });

    const result = await response.json();
    console.log('Expense added to Google Sheet:', result);
  } catch (error) {
    console.error('Error adding expense to Google Sheet:', error);
  }
};

export default addExpenseToSheet;
